*{
    margin: 0;
    padding: 0;
}
.background{
    background-color: #CCCCFF;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.textareacontroller{
    background-color: white;
    border: 1px solid gray;
    margin-top: 1rem;
    width: 90vw;
    min-height: 70vh;
}
.textareacontroller p{
    color: black;
}
.micbtn{
    background-color: #FF7F50;
    width: 10rem;
    height: 10rem;
    margin-top: 1rem;
    border-radius: 10rem;
    border: 3px solid white;
    cursor: pointer;
}
.micbtn:active{
    background-color: #DE3163;
    
}

.trash{
    width: 5rem;
    height: 5rem;
    position: absolute;
    bottom: 10vh;
    right: 3vw;
    border-radius: 10rem;
    background-color: black;
    cursor: pointer;
    border: 3px solid white;
}
.trash:active{
    background-color: gray;
}

